//  
.elements-center() {
  display: flex;
  justify-content: center;
  align-items: center;
}

//  
.text-size(@font-size, @line-height) {
  font-size: @font-size;
  line-height: @line-height;
}

// 
.size(@width, @height) {
  width: @width;
  height: @height;
}

//  
.menu-button-style() {
  .size(56px, 56px);
  background-color: transparent;
  border: none;
  cursor: pointer;

  &::before {
    content: "";
    position: absolute;
    background-color: @accent-color;
    height: 4px;
    top: 17px;
    left: 13px;
    right: 20px;
    box-shadow: 0 9px 0 0 @accent-color, 0 18px 0 0 @accent-color;
  }
}

//  
.navigation-link-style() {
  display: block;
  padding: 17px 20px 12px;
  text-transform: uppercase;
  text-align: center;
  font-weight: 700;
  color: @main-color;
  background-color: @accent-color;
}

//   
.input-style() {
  display: block;
  font-family: inherit;
  font-weight: 700;
  padding: 16px;
  box-sizing: border-box;
  border: none;
  background-color: @secondary-color;

  &::placeholder {
    color: fade(@black, 30%);
  }
}

// 
.button-style() {
  display: block;
  font-family: inherit;
  .text-size(21px, 26px);
  text-transform: uppercase;
  text-align: center;
  font-weight: 700;
  color: @main-color;
  background-color: @accent-color;
  border: none;
  padding: 16px 0;
  cursor: pointer;
}
